:root {
  --one: #ffffff;
  --two: #3e3f41;
  --three: #eeeeee;
  --four: #000000;
  --five: #222222;
  --six: #007bff;
  --seven: #6c757d;
  --eigth: #e2e2e2;
  --nine: #54707b;
  --ten: #ffd814;
  --eleven: #fcd200;
  --twelve: #eef6f6;
  --thirteen: #363636;
  --fourteen: #797979;
  --fiveteen: #444444;
  --font: "Roboto", sans-serif;
  --shadow: 0 2px 5px 0 rgba(213, 217, 217, 0.5);
  --shadowTwo: 0 0 10px #cccccc;
}
.Benefit {
  min-width: 320px;
  width: 100%;
  background-color: var(--three);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.Benefit__one,
.Benefit__two,
.Benefit__three {
  text-align: center;
  margin: 5px 5px;
}
.Benefit__one--h2,
.Benefit__two--h2,
.Benefit__three--h2 {
  color: var(--four);
  font-weight: bold;
  font-size: 1rem;
}
.Benefit__one--h2--icon,
.Benefit__two--h2--icon,
.Benefit__three--h2--icon {
  font-size: 2rem;
  margin-bottom: -10px;
  margin-right: 5px;
}
.Benefit__one--h3,
.Benefit__two--h3,
.Benefit__three--h3 {
  color: var(--four);
  font-weight: lighter;
  font-size: 0.9rem;
  margin-top: -10px;
}
